}

.formWrapperLogin {
    width: 75%;
    background-color: #ffffff;
}

.formTitle {
    font-size: 40px;
    font-weight: 200;
    position: absolute;
    text-align: center;
    -webkit-transition: all 0.9s;
    -o-transition: all 0.9s;
    transition: all 0.9s;
}

.formTitleLogin {
    width: 100%;
    top: 5%;
    left: 0;
    color: white;
