    left: 550px;
    /* transition: opacity 0.5s, -webkit-transform 0.5s; */
}

.formLogin {
    /* -webkit-transform: translateX(-200%);
          transform: translateX(-200%); */
    opacity: 1;
}

.formInputContainer {
    padding: 15px 0;
    /* position: absolute; */
}

.formInputWrapper {
    width: 350px;
    position: relative;
}

.formInputWrapper .formInput {
    width: 100%;
}

.formInput {
    width: 350px;
    padding: 10px 25px;
    border: 1px solid #dddddd;
    font-family: Montserrat, sans-serif;
    -webkit-box-shadow: 0 2px 16px 0 #F09819;
