* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.about {
  height: 100%;
}
.or{
  color: #ffffff;
}
.about {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-family: Montserrat, sans-serif;
  text-transform: uppercase;
  background-color: #F09819;
}
.formContainer {
  width: 800px;
  height: 400px;
  border-radius: 10%;
  border-radius: 3px solid #dddddd;
}
.formWrapper {
  height: 100%;
  float: left;
  position: relative;
  overflow: hidden;
  -webkit-transition: width 0.5s, background-color 0.5s;
  -o-transition: width 0.5s, background-color 0.5s;
  transition: width 0.5s, background-color 0.5s;
}
 .signup_active .formWrapperSignup {
  width: 75%;
  background-color: #ffffff;
}
.formTitle {
  font-size: 40px;
  font-weight: 200;
  position: absolute;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}
.formTitleSignup {
  width: 100%;
  top:0%;
  left: 530px;
  color: white;
  /* -webkit-transform: translateY(-50%);
          transform: translateY(-50%); */
}
.signup_active .formTitleSignup {
  width: 0;
  color:black;
  top: 50px;
  left: -200px;
  -webkit-transform: none;
          -ms-transform: none;
      transform: none;
}
.form {
  position: absolute;
  top: 110px;
  left: 460px;
  transition: opacity 0.5s, -webkit-transform 0.5s;
}
.formSignup {
  opacity: 1;
}
.signup_active .formSignup {
}
.formInputContainer {
  padding: 15px 0;
}
.formInputWrapper {
  width: 350px;
  position: relative;
}
.formInputWrapper .formInput {
  width: 100%;
}
.formInput {
  width: 350px;
  padding: 10px 25px;
  border: 1px solid #dddddd;
  font-family: Montserrat, sans-serif;
  -webkit-box-shadow: 0 2px 16px 0 #F09819;
          box-shadow: 0 2px 16px 0 #F09819;
  outline: none;
  /* border-radius: 35%; */
}
.formInput::-webkit-input-placeholder {
  color: #7f848c;
}
.formInput::-moz-placeholder {
  color: #7f848c;
}
.formInput:-ms-input-placeholder {
  color: #7f848c;
}
.formInput:-moz-placeholder {
  color: #7f848c;
}
.formButton {
  padding: 15px 100px;
  position: relative;
  background: #ffffffs;
  border: 1px solid #dddddd;
  font-family: Montserrat, sans-serif;
  color:#F09819;
  text-transform: uppercase;
  -webkit-transition: color 0.3s, border-color 0.3s, -webkit-box-shadow 0.3s;
  transition: color 0.3s, border-color 0.3s, -webkit-box-shadow 0.3s;
  -o-transition: color 0.3s, border-color 0.3s, box-shadow 0.3s;
  transition: color 0.3s, border-color 0.3s, box-shadow 0.3s;
  transition: color 0.3s, border-color 0.3s, box-shadow 0.3s, -webkit-box-shadow 0.3s;
  cursor: pointer;
  /* border-radius: 35%; */
}
.formButton:hover {
  background-color: #ffffff;
  color: #F09819;
  border-color: #F09819;
  -webkit-box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.1);
}
.formInputShow {
  position: absolute;
  top: 50%;
  right: 20px;
  font-size: 14px;
  color: black;
  font-weight: 300;
  cursor: pointer;
}


